.goal-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    background: linear-gradient(to bottom, #eaf2f9 60%, #eaf2f990 100%), url("../../assets/goal-bg.jpg") center center no-repeat ;
    background-position: 50% 100%;
    background-size: 100%;
    width: 90%;
    min-height: 37.24vw;
    border-radius: 1.56vw;
    margin: 1.56vw auto 0 auto;
    padding: 1.3vw 1.3vw 4.17vw 1.82vw;
    position: relative;
    box-shadow: -10px -10px 20px 0px rgba(252,254,255,1), 10px 10px 20px 0px rgba(190,203,216,1);

    h2 {
        font-family: 'gill-sans-nova', sans-serif;
        font-weight: 600;
        font-style: normal;
        font-size: 2.08vw;
        color: #3E4F70;
        line-height: 0%;
    }

    .form-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        width: 99%;
        margin-top: 2.6vw;
        border-radius: 0.26vw;
        padding-left: 1.04vw;;
        box-shadow: -10px -10px 20px 0px rgba(252,254,255,1), 10px 10px 20px 0px rgba(190,203,216,1);

        .cont-save-input {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            width: 100%;

            .input {
                width: 70%;
            }
            .button {
                width: 30%;
            }
        }
    }
}