.inst-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    background: linear-gradient(to bottom, #eaf2f9 60%, #eaf2f990 100%), url("../../assets/bg-instructions.jpg") center center no-repeat ;
    background-position: 50% 100%;
    background-size: 100%;
    width: 90%;
    min-height: 37.24vw;
    border-radius: 1.56vw;
    margin: 1.56vw auto 0 auto;
    padding: 1.3vw 1.3vw 10.17vw 1.82vw;
    position: relative;
    box-shadow: -10px -10px 20px 0px rgba(252,254,255,1), 10px 10px 20px 0px rgba(190,203,216,1);

    h2 {
        font-family: 'gill-sans-nova', sans-serif;
        font-weight: 600;
        font-style: normal;
        font-size: 2.08vw;
        color: #3E4F70;
        line-height: 0%;
    }

    .inst-container {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        width: 100%;
        margin-top: 2.6vw;

        .step {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            width: 100%;
            margin-bottom: 1.04vw;

            h5 {
                font-size: 1.08vw;
                font-weight: 600;
                color: #1F2838;
                margin-top: 0 !important;
                margin-bottom: 0 !important;
                width: 30%;
            }
    
            p {
                font-size: 0.83vw;
                line-height: 150%;
                font-weight: 400;
                color: #333333;
                margin-block-start: 0 !important;
                margin-block-end: 0.56vw !important;
            }
        }
    }
}